@import url("~leaflet/dist/leaflet.css");

body,
#root,
.leaflet-container {
  height: 100vh;
  width: 100vw;
  background-color: #9fbfc6

}

.zoom-slider {
  position: absolute;
  top: 20px;
  right: 20px;
  z-index: 1000;
}

.basemaps-container {
  position: absolute;
  top: 20px;
  right: 20px;
  z-index: 1000;
  height: auto;
  width: auto;
  border-radius: 4px;
  background-color: white;
}
.basemaps-container select {
  width: 200px;
  height: 20px;
}